import React, { FC, useCallback, useState, useEffect, ChangeEvent, useMemo } from 'react';
import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Avatar,
  Card,
  CardHeader,
  CardContent,
  Hidden,
  Tabs,
  Tab
} from '@material-ui/core';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import { WrappedMineItem, BTCSTMineItem, LPMineItem, PoolType } from 'src/types/mine';
import { useTranslation } from 'react-i18next';
import { useDailyPool } from 'src/hooks/useDailyPool';
import { BTCST, BTCB, TBTC, TDOGE, DOGE } from 'src/constants/token';
import { FixedNumber } from 'ethers';
import { useActiveWeb3React } from 'src/hooks/useActiveWeb3React';

function apyDisplay(apy: { data: string; diff: FixedNumber }) {
  const diffResult =
    apy.diff.isNegative() || apy.diff.isZero()
      ? ''
      : `(${apy.diff.toString()}% from inflation)`;

  return (
    <>
      <span>{apy.data}</span>
      {diffResult && <span>{diffResult}</span>}
    </>
  );
}

interface MineListProps {
  className?: string;
  list: BTCSTMineItem[] | LPMineItem[] | WrappedMineItem[];
}

const useStyles = makeStyles(theme => ({
  root: {},
  avatar: {
    height: 22,
    width: 22,
    marginRight: theme.spacing(1)
  },
  tableRow: {
    borderBottom: 'solid 1px #000',
    cursor: 'pointer'
  },
  cardContent: {
    padding: '0'
  }
}));

const poolTypes: PoolType[] = ['live', 'finished'];

const MineList: FC<MineListProps> = ({ className, list }) => {
  const classes = useStyles();
  const history = useHistory();
  const { library } = useActiveWeb3React();
  const { t } = useTranslation();
  const [latestBlockNumber, setLatestBlockNumber] = useState(0);
  const [currentTab, setCurrentTab] = useState<PoolType>(poolTypes[0]);

  const handleTabsChange = useCallback(
    (event: ChangeEvent<{}>, value: PoolType): void => {
      setCurrentTab(value);
    },
    []
  );

  useEffect(() => {
    if (!library) {
      return;
    }

    library.getBlockNumber().then(data => {
      setLatestBlockNumber(data);
    });
  }, [library]);

  const filteredList = useMemo(() => {
    return list.filter(item => {
      if (currentTab === 'live') {
        return !item.endBlock || item.endBlock >= latestBlockNumber;
      }

      if (currentTab === 'finished') {
        return item.endBlock && item.endBlock < latestBlockNumber;
      }

      return true;
    });
  }, [list, currentTab, latestBlockNumber]);

  const { apy: btcstToBtcbApy } = useDailyPool(BTCST, BTCB);
  const { apy: btcstToBtcstApy } = useDailyPool(BTCST, BTCST);
  const { apy: btcstToTbtctApy } = useDailyPool(BTCST, TBTC);
  const { apy: BTCBToTbtctApy } = useDailyPool(BTCB, TBTC);
  const { apy: TBTCToTbtctApy } = useDailyPool(TBTC, TBTC);

  const { apy: btcstToTDOGEApy } = useDailyPool(BTCST, TDOGE);
  const { apy: TBTCToTDOGEApy } = useDailyPool(TBTC, TDOGE);
  const { apy: TDOGEToTDOGEApy } = useDailyPool(TDOGE, TDOGE);
  const { apy: DOGEToTDOGEApy } = useDailyPool(DOGE, TDOGE);

  const handleRowClick = useCallback(
    item => {
      history.push(
        `/mine/stake?pair=${item.stakeToken.symbol}/${item.targetToken.symbol}`
      );
    },
    [history]
  );

  return (
    <Card>
      <CardHeader title={t('mine.list')} />
      <CardContent className={classes.cardContent}>
        <Box mt={1}>
          <Tabs
            onChange={handleTabsChange}
            scrollButtons="auto"
            textColor="secondary"
            value={currentTab}
            variant="scrollable"
          >
            {poolTypes.map(tab => (
              <Tab key={tab} label={t(`mine.tab.${tab}`)} value={tab} />
            ))}
          </Tabs>
        </Box>

        <PerfectScrollbar>
          <Table className={className}>
            <TableHead>
              <TableRow className={classes.tableRow}>
                <TableCell>{t('mine.stake')}</TableCell>
                <TableCell>{t('mine.mine')}</TableCell>
                <TableCell>{t('mine.apy')}</TableCell>
                <TableCell>{t('mine.cycle')}</TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {filteredList.map(item => (
                <TableRow
                  onClick={() => handleRowClick(item)}
                  hover
                  key={`${item.stakeToken.name}_${item.targetToken.name}`}
                  className={classes.tableRow}
                >
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      {item.stakeToken.logoURI && (
                        <Avatar
                          className={classes.avatar}
                          src={item.stakeToken.logoURI}
                        />
                      )}
                      <div>{item.stakeToken.name}</div>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      <Hidden smDown>
                        {item.targetToken.logoURI && (
                          <Avatar
                            className={classes.avatar}
                            src={item.targetToken.logoURI}
                          />
                        )}
                      </Hidden>
                      <div>{item.targetToken.name}</div>
                    </Box>
                  </TableCell>
                  <TableCell>
                    {currentTab === 'finished' ? (
                      '-'
                    ) : (
                      <span>
                        {item.stakeToken.symbol === 'btcst' &&
                          item.targetToken.symbol === 'btcb' &&
                          (btcstToBtcbApy ? apyDisplay(btcstToBtcbApy) : '-')}
                        {item.stakeToken.symbol === 'btcst' &&
                          item.targetToken.symbol === 'btcst' &&
                          (btcstToBtcstApy ? apyDisplay(btcstToBtcstApy) : '-')}
                        {item.stakeToken.symbol === 'btcst' &&
                          item.targetToken.symbol === 'tbtc' &&
                          (btcstToTbtctApy ? apyDisplay(btcstToTbtctApy) : '-')}
                        {item.stakeToken.symbol === 'btcb' &&
                          item.targetToken.symbol === 'tbtc' &&
                          (BTCBToTbtctApy ? apyDisplay(BTCBToTbtctApy) : '-')}
                        {item.stakeToken.symbol === 'tbtc' &&
                          item.targetToken.symbol === 'tbtc' &&
                          (TBTCToTbtctApy ? apyDisplay(TBTCToTbtctApy) : '-')}

                        {item.stakeToken.symbol === 'btcst' &&
                          item.targetToken.symbol === 'tdoge' &&
                          (btcstToTDOGEApy ? apyDisplay(btcstToTDOGEApy) : '-')}
                        {item.stakeToken.symbol === 'tbtc' &&
                          item.targetToken.symbol === 'tdoge' &&
                          (TBTCToTDOGEApy ? apyDisplay(TBTCToTDOGEApy) : '-')}
                        {item.stakeToken.symbol === 'doge' &&
                          item.targetToken.symbol === 'tdoge' &&
                          (DOGEToTDOGEApy ? apyDisplay(DOGEToTDOGEApy) : '-')}
                        {item.stakeToken.symbol === 'tdoge' &&
                          item.targetToken.symbol === 'tdoge' &&
                          (TDOGEToTDOGEApy ? apyDisplay(TDOGEToTDOGEApy) : '-')}
                      </span>
                    )}
                  </TableCell>
                  <TableCell>{t('mine.' + item.rewardCycle)}</TableCell>
                  <TableCell>
                    <MoreVertIcon />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </PerfectScrollbar>
      </CardContent>
    </Card>
  );
};

MineList.propTypes = {
  className: PropTypes.string
};

export default MineList;
