import BTCSTMineList from './BTCST';
import LPMineList from './LP';
import WrappedMineList from './WrappedAsset';
import RewardList from './rewardList';
import MineList from './list';
import { list } from 'src/constants/mine';
import { useMineState } from 'src/hooks/useMineState';
import { Alert } from '@material-ui/lab';
import GetBTCSTCard from 'src/components/GetBTCSTCard';
import MarketMake from 'src/components/MarketMake';
import { wrappedTokens } from 'src/constants/mine'
import GetWrappedAssetCard from 'src/components/GetWrappedAssetCard';
import LoadingBox from 'src/components/LoadingBox';
import { useTranslation } from 'react-i18next';
import { RewardItem, PoolType } from 'src/types/mine';

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    // paddingTop: theme.spacing(3),
    paddingBottom: '80px'
  },
}));

// const tabs = [
//   { value: 'btcst', label: 'BTCST' },
//   { value: 'wrapped_assets', label: 'Wrapped Assets' },
//   { value: 'lp', label: 'LP' },
// ];



const MineView: FC = () => {
  const classes = useStyles();
  const {t} = useTranslation();
  const [firstTimeLoading, setFirstTimeLoading] = useState(true);

  // const [currentTab, setCurrentTab] = useState<PoolType>(poolTypes[0]);

  // const handleTabsChange = useCallback((event: ChangeEvent<{}>, value: PoolType): void => {
  //   setCurrentTab(value);
  // }, []);
  const { rewardList, asyncClaimClick, asyncClaimOneClick } = useMineState();

  useEffect(() => {
    if (rewardList.result) {
      setFirstTimeLoading(false);
    }
}, [rewardList.result]);

  return (
    <Page className={classes.root} title={t("global.title")}>
      <Container maxWidth={false}>
        <Header title={t("mine.title")} />
        {/* {
          rewardList.error && (
            <Alert variant="filled" severity="error">
              { rewardList.error.message}
            </Alert>
          )
        } */}
        <LoadingBox
          loading={rewardList.loading && firstTimeLoading}
          showNoData={!rewardList.result && firstTimeLoading}
          showNoDataContent={<Typography variant="h4" color="textPrimary">
            {t("mine.noData")}
          </Typography>}
        >
          <RewardList list={rewardList.result} asyncClaimClick={asyncClaimClick} asyncClaimOneClick={asyncClaimOneClick} />
        </LoadingBox>
        <Box mt={3}>
          {/* {currentTab === 'btcst' && <BTCSTMineList />}
          {currentTab === 'wrapped_assets' && <WrappedMineList />}
          {currentTab === 'lp' && <LPMineList />} */}
          <MineList list={list} />

        </Box>
        <Box mt={5}>
          <Grid container spacing={2}>
            <Grid item sm={4} xs={12}>
              <GetBTCSTCard />
            </Grid>
            <Grid item sm={4} xs={12}>
              <GetWrappedAssetCard tokens={wrappedTokens} />
            </Grid>
            <Grid item sm={4} xs={12}>
              <MarketMake />
            </Grid>
          </Grid>
        </Box>
      </Container>
    </Page>
  );
};

export default MineView;
