import React, { FC, useCallback, useEffect, useMemo, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import PropTypes from 'prop-types';
import {
  Box,
  makeStyles,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Avatar,
  CardHeader,
  CardContent,
  CardActions,
  CircularProgress
} from '@material-ui/core';
import isEmpty from 'lodash/isEmpty';
import { RewardItem } from 'src/types/mine';
import { formatBalance } from 'src/utils/format';
import ClaimButton from '../airdrop/ClaimButton';
import { BigNumber, FixedNumber, utils } from 'ethers';
import currency from 'currency.js';
import { useTranslation } from 'react-i18next';
import { getPriceBySymbol } from 'src/utils/price';
import { useRecoilValue } from 'recoil';
import { tokenPriceListAtom, tokenPriceLoadingAtom } from 'src/atoms';
import LoadingBox from 'src/components/LoadingBox';
import LoadingButton from 'src/components/LoadingButton';

interface RewardListProps {
  className?: string;
  list: RewardItem[];
  asyncClaimClick: any;
  asyncClaimOneClick: any;
}

const useStyles = makeStyles(theme => ({
  root: {},
  table: {},
  avatar: {
    height: 22,
    width: 22,
    marginRight: theme.spacing(1)
  },
  claim: {
    marginLeft: 'auto',
    borderRadius: '0px 0px 6px 6px'
  },
  tableRow: {
    borderBottom: 'solid 1px #000'
  },
  tableCell: {
    wordBreak: 'break-all'
  },
  cardContent: {
    padding: '0'
  },
  cardAction: {
    padding: 0
  },
  claimButton: {
    width: '100px',
    height: '30px',
    background: '#222222',
    boxShadow: '0px 2px 4px 0px #000000',
    color: '#fff'
  }
}));

const RewardList: FC<RewardListProps> = ({
  className,
  list,
  asyncClaimClick,
  asyncClaimOneClick,
  ...rest
}) => {
  const classes = useStyles();
  const { t } = useTranslation();
  const tokenPrice = useRecoilValue(tokenPriceListAtom);
  const tokenPriceLoading = useRecoilValue(tokenPriceLoadingAtom);
  const [rewards, setRewards] = useState<RewardItem[]>([]);

  const canClaim = useMemo(() => {
    if (!rewards) {
      return false;
    }

    return rewards
      .reduce((current, next) => {
        return current.add(next.rewardAmount);
      }, BigNumber.from('0'))
      .gt(BigNumber.from('0'));
  }, [rewards]);

  useEffect(() => {
    if (list) {
      setRewards(list);
    }
  }, [list]);

  return (
    <Box>
      <Card>
        <CardHeader title={t('mine.rewardTitle')} />
        <CardContent className={classes.cardContent}>
          <PerfectScrollbar>
            <Table className={classes.table}>
              <TableHead>
                <TableRow className={classes.tableRow}>
                  <TableCell>{t('mine.name')}</TableCell>
                  <TableCell>{t('mine.rewards')}</TableCell>
                  <TableCell>{t('mine.approxUsd')}</TableCell>
                  <TableCell></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {rewards.map(item => {
                  const price = getPriceBySymbol(tokenPrice, item.token.symbol);
                  return (
                    <TableRow
                      hover
                      key={item.token.name}
                      className={classes.tableRow}
                    >
                      <TableCell>
                        <Box display="flex" alignItems="center">
                          {item.token.logoURI && (
                            <Avatar
                              className={classes.avatar}
                              src={item.token.logoURI}
                            />
                          )}
                          <div>{item.token.name}</div>
                        </Box>
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        {item.rewardAmount
                          ? formatBalance(
                              item.rewardAmount,
                              item.token.decimals
                            )
                          : '-'}
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        <LoadingBox
                          loading={tokenPriceLoading}
                          p={0}
                          size="20px"
                          loadingAlign="left"
                        >
                          {!tokenPriceLoading && item.rewardAmount && price
                            ? currency(
                                FixedNumber.from(
                                  utils.formatUnits(
                                    item.rewardAmount,
                                    item.token.decimals
                                  )
                                )
                                  .mulUnsafe(FixedNumber.from('' + price))
                                  .toString(),
                                { precision: 4 }
                              ).format()
                            : '-'}
                        </LoadingBox>
                      </TableCell>
                      <TableCell className={classes.tableCell}>
                        <LoadingButton
                          color="primary"
                          variant="contained"
                          className={classes.claimButton}
                          disabled={
                            !item.rewardAmount || item.rewardAmount.isZero()
                          }
                          onClick={() =>
                            asyncClaimOneClick.execute(item.token.address)
                          }
                          pending={asyncClaimOneClick.loading}
                        >
                          {t('mine.claim')}
                        </LoadingButton>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
            </Table>
          </PerfectScrollbar>
        </CardContent>
        <CardActions className={classes.cardAction}>
          <ClaimButton
            disabled={!canClaim}
            className={classes.claim}
            onClick={asyncClaimClick.execute}
            pending={asyncClaimClick.loading}
          />
        </CardActions>
      </Card>
    </Box>
  );
};

RewardList.propTypes = {
  className: PropTypes.string
};

export default RewardList;
